.pagination-wrap{
    display: flex;
    justify-content: space-between;
}
.page-btn{
    font-weight: 500;
    background: white;
    border: 1px solid #3F3F3F;
    border-radius: 3px;
    padding: 5px 10px;
}
.pagination-text {
    font-size: 13px;
    font-weight: 500;
    background: white;
    border: none;
    }
.text-disabled{
    color: grey;
}